import {Locale} from "core/logic/cnst"
import {WordsEn} from "games/polyglot/wordmaster/wordsEn"
import {WordsRu} from "games/polyglot/wordmaster/wordsRu"
import {LocaleUtl} from "games/utils/localeUtl"
import * as _ from "lodash"

export type SynonymsWordSet = string[][]

export module AnagrammaWords {
  const localeData = {
    ru: WordsRu.data,
    en: WordsEn.data
  }

  export const items = (locale: Locale) => {
    const data = LocaleUtl.getData(localeData, locale)
      .filter(x => x[0].indexOf(" ") < 0)
      .map(([head, ...tail]) => [head.toLowerCase(), ...(tail.filter(x => x != ""))])
    return _.groupBy(data, x => x[0].length)
  }

}



