import {translationService} from "core/services"
import {Sizes} from "core/ui/common/sizes"
import {TextPanelComponent} from "core/ui/components/games/gameUI/textPanelComponent"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {screenSizeService} from "core/ui/services/instances"
import {BaseWordMainComponent} from "games/polyglot/common/components/baseWordMainComponent"
import {TypeWriterComponent} from "games/polyglot/wordmaster/components/typeWriterComponent"
import {Colors} from "games/polyglot/wordmaster/images"
import {Translations} from "games/polyglot/wordmaster/translations"
import {Task} from '../logic'

export class MainComponent extends BaseWordMainComponent {
  positions2 = Sizes.create({
    descY: 200,
    wordWidth: {width: 900, height: 120}
  })

  positionsPortrait2 = Sizes.update(this.positions2, {
    descY: 150,
    wordWidth: {width: 640, height: 120}
  })

  getPositions2 = () => screenSizeService.isPortrait ? this.positionsPortrait2 : this.positions2

  task: Task
  private textPanel: TextPanelComponent

  onInit(): void {

    this.hintComponent.show = false

    this.positions = Sizes.update(this.positions, {
      tutorial: {y: 40},
      mainGroup: {y: 180},
      wordlistOffset: {x: 0, y: (this.isTutorial && screenSizeService.isPortrait) ? -140 : 0}
    })
    this.positionsPortrait = Sizes.update(this.positionsPortrait, {
      tutorial: {y: 20},
      mainGroup: {y: 190},
      wordList: [Sizes.widthNormal / 2, 900],
      typewriter: [650, 190],
    })
    this.typewriter = this.mainGroup.add(this.createComponent(TypeWriterComponent))
      .setPos(this.getPositions().typewriter)

    super.onInit()
    this.textPanel = this.mainGroup.add(new TextPanelComponent(
      {size: this.getPositions2().wordWidth, color: Colors.panel}))
      .updatePos(Sizes.w2, this.getPositions2().descY).anchorCenter()

  }

  createTutorial() {
    return this.add(this.createComponent(TutorialTextComponent)).make({panel: {color: Colors.panel}})
  }

  onNewTask(task: Task) {
    this.task = task
    this.textPanel.setProps(this.task.desc)
    this.renderTask([task.anagramma.word], [task.anagramma])
    if (this.tutorial) {
      this.bringToTop(this.tutorial)
      const key = task.isHide ? Translations.title2 : Translations.title1
      this.tutorial.setProps(translationService.getTranslation(key))
    }
  }

}




