import {ImagesMetaData} from "core/types/logic"
import {WordImages} from "games/polyglot/common/wordImages"

export module Colors {
  export const panel = 0x174917
}

export module Images {

  export const type_keyboard = "type_keyboard"
  export const type_chert = "type_chert"
  export const sprites = WordImages.sprites.concat([
    type_keyboard,
    type_chert
  ])
  export const data: ImagesMetaData = {sprites}

}




