import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {Img} from "core/ui/controls/img"
import {tuple} from "core/utils/tuples"
import {BaseTypeWriterComponent} from "games/polyglot/common/components/baseTypewriterComponent"
import {Images} from "../images"

const durations = {
  animation: 500
}

export class TypeWriterComponent extends BaseTypeWriterComponent {
  positions2 = Sizes.create({
    keyboardY: 540
  })
  private keyboard: Img

  onInit(): void {
    this.positions = Sizes.update(this.positions, {
      text: tuple(380, 285),
      maxWordWidth: 420,
      handsY: 470
    })
    super.onInit()
    this.keyboard = this.addImage([Images.type_keyboard, [this.height / 2, this.positions2.keyboardY]])
      .anchorCenter().fluentUpdate(x => {
        x.symmetricScale = 0
        x.alpha = 0
      })
    this.addHands()
    this.context.uiService.mainComponent.onIntroComplete.addOnce(() => this.animationKeyboard())
  }

  animationKeyboard() {
    Tweens.start({
      target: this.keyboard,
      to: {
        symmetricScale: 1,
        alpha: 1
      },
      easing: Phaser.Easing.Sinusoidal.InOut,
      duration: durations.animation
    })
  }
}



