import {ITask} from "core/logic/index"
import {GameLogic} from "core/types/gameLogic"
import {UniqSeq} from "core/utils/circleSeq"
import {RandomGenerator} from "core/utils/index"
import {Strings} from "core/utils/strings"
import {tuple} from "core/utils/tuples"
import {Anagramma} from "games/polyglot/common/logic"
import {Level, ModifyType} from "games/polyglot/wordmaster/levels"
import {AnagrammaWords} from "games/polyglot/wordmaster/words"
import * as _ from 'lodash'

const letters = "абвгдеёжзийклмнопрстуфхцчшщъыьэюя".split("")

export interface Task extends ITask {
  anagramma: Anagramma
  isHide: boolean
  desc: string
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()
  uniqSeq = new UniqSeq(10)
  index = 0

  createAnagramma(word: string) {
    return {
      anagramma: this.rng.derangementString(word),
      word
    }
  }

  addRandomLetter(anagramma: string) {
    const index = this.rng.int(anagramma.length)
    return Strings.insert(anagramma, this.rng.sample(letters, anagramma.split("")), index)
  }

  modify(type: ModifyType, anagramma: Anagramma): Anagramma {
    switch (type) {
      case "hide": {
        const firstLetter = anagramma.word[0]
        const duplicateLetters = _.toPairs(_.groupBy(anagramma.word.split(""), x => x))
          .filter(([k, v]) => v.length > 1).map(x => x[0])
        const noLetters = [...duplicateLetters, firstLetter]
        const noLettersIndexes = anagramma.anagramma.split("").map((l, i) => tuple(l, i))
          .filter(([l, i]) => _.includes(noLetters, l))
          .map(x => x[1])
        const hideIndex = this.rng.uniqArray(anagramma.word.length, 1, 0, noLettersIndexes)
        return {...anagramma, hideIndex: hideIndex[0] || 0}
      }
      case "extra":
        return {...anagramma, anagramma: this.addRandomLetter(anagramma.anagramma)}
    }
  }

  items = AnagrammaWords.items(this.gameData.settings.custom.locale)

  nextTask(level: Level) {
    const words = this.items[level.wordLength]
    const [word, ...descriptions] = this.uniqSeq.nextFrom(words)
    const desc = this.rng.sample(descriptions)
    const modify: ModifyType[] = this.isTutorial && this.index > 0 ? ["hide"] : level.modify
    const anagramma = modify.reduce((a, x) => this.modify(x, a), this.createAnagramma(word))
    const answers = {seq: word.split("").map(x => x + "0")}
    const isHide = _.includes(modify, "hide")
    const task = {
      anagramma,
      desc,
      isHide,
      answers
    }
    this.index += 1
    return task
  }

}





