import {createLevelsFromArray} from "core/types/logic"
import * as _ from "lodash"

export type ModifyType = "hide" | "extra"

export interface Level {
  wordLength: number
  modify: ModifyType[]
}

function level(wordLength: number, modify: ModifyType[] = []): Level {
  return {wordLength, modify}
}

const lengthList = _.range(5, 9)
const modifyList: ModifyType[][] = [[], ["hide"]]

export const levels = createLevelsFromArray([level(4), ..._.flatMap(lengthList, (l => modifyList.map(m => level(l, m))))])



